/******************** Variables *********************/

$pageWidth: 1180px;
//colors
$footer-font-color: #515151;
$footer-font-size: .8em;
$button-color: ffc000;
$white: #ffffff;
$black: #1b1b1b;
$primary_color: #43779e;
$secondary_color: #9aabbc;
//fonts
$footer-font-size: .8em;
$font-weight-base: 600;

/******************** Page Resets *********************/

/** Removes various page elements and styles to set up a blank canvas **/

* {
  box-sizing: border-box;
}

#head,
#page-title,
#floor {
  display: none;
}

/** Set to the width of your project **/
#content {
  width: 100%;
}

#main {
  margin: 0;
  padding: 0;
}

#content > .clearfix {
  background: transparent;
  box-shadow: none;
}

/******************** Utilty *********************/

/*  Text meant only for screen readers.
    Apply this style to any text that is being replaced by an image */

.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: inherit;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: inherit;
  display: block;
  font-size: 1em;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/********************* Base **********************/
p, li, h3, h1 {
  color: $white;
  font-weight: $font-weight-base;
}

/******************** Layout *********************/
/* footer */
footer {
  background-color: $black;
  font-size: $footer-font-size;
  text-align: center;
  li {
    text-align: center;
    list-style: none;
    color: $footer-font-color;
  }
}

.counter-wrapper {
  background: $primary_color;
  color: $white;
  margin: 0;
  width: 100%;

  .section_report {
    margin: 0 auto;
    width: 100%;

    .report {
      padding: 2.2em;
      .inner-report {
        max-width: 200px;
        margin: 0 auto;
      }
    }

    hr {
      display: inline-block;
      width: 2em;
      margin: 0 auto;
      margin-bottom: 17px;
      border: none;
      height: 1px;
      background: $white;
    }

    .top-row {
      .report {
        margin: 0;
        text-align: center;

        &:nth-child(odd) {
          background-color: $secondary_color;
          color: black;

          p {
            color: $black;
          }
          hr {
            background: $black;
          }
        }
      }
    }
    .bottom-row {
      .report {
        margin: 0;
        text-align: center;

        &:nth-child(even) {
          background-color: $secondary_color;
          color: black;

          p {
            color: $black;
          }

          hr {
            background: $black;
          }
        }
      }
    }

  }
  .counter {
    width: 100%;
    font-size: 3.6em;
    padding: .5em;
    margin: 0 auto;
    position: relative;

    span {
      position: absolute;
      top: 8px;
      left: 50;
      display: inline-block;
      font-size: 0.4em;
      line-height: 2em;
    }

  }
}

.video-outer-wrap {
  text-align: center;
  background-color: $primary_color;
  .video-wrap {

    background: $primary_color;

    img {
      display: block;
      width: 100%;
      height: auto;

      -webkit-transition: 500ms;
      -moz-transition: 500ms;
      -o-transition: 500ms;
      transition: 500ms;

      &:hover {
        opacity: 0.8;
        -webkit-transition: 500ms;
        -moz-transition: 500ms;
        -o-transition: 500ms;
        transition: 500ms;
      }
    }
  }
}

/* video-outer-wrap */

.copy-form-wrapper {
  background-image: url("../img/header-sc.jpg");
  background-size: cover;
  background-position: center;
  padding: 3.6em 0;

  .list-wrap {
    text-align: center;

    h1 {
      text-align: left;
    }

    ul {
      margin: 2em 0 3em 1.2em;
      text-align: left;
      list-style-position: outside;

      li {
        margin: 1em 0;
        line-height: 1.4em;
        font-size: 16px;
      }
    }
  }

  .form-copy-wrapper {
    padding: 1.2em;
    background-color: rgba(66, 117, 158, .4);
    max-width: 400px;
    margin: 0 auto;
    position: relative;

    h3 {
      text-align: center;
    }

    .gform_wrapper {
      width: 310px;
      margin: 1.8em auto 0 auto;

      input[type="submit"].gform_button {
        color: $black !important;
        width: 80%;
        border-radius: 0;
        margin-top: 2em;
        margin-bottom: 2em;

        &:hover {
          color: $white !important;
        }
      }
    }
    /* gform_wrapper */

    .camp-book {
      position: absolute;
      right: 82px;
      bottom: 13px;
      display: block;
    }
  }
  /* form-copy-wrapper */
}

/* copy-form-wrapper */

.header {
  padding-top: 1em;
  text-align: center;

  .logo {
    margin-top: 1.2em;

  }

  .phone {
    color: $primary_color;
    font-weight: bold;
    font-style: italic;
    font-size: 1.4em;
    margin: 1.2em;

    a, a:visited {
      color: $primary_color;

      &:hover, &:active, &:focus {
        color: $black;
      }
    }
  }
}

/* header */

/******************** Form **********************/
/* Footer Form */
.form-wrapper {
  background-color: #1f3a4d;
  color: $white;
  text-align: center;
  width: 100%;
  padding: 1.2em 0 2.4em;

  h3 {
    padding-top: 1em;
    color: $white;
    font-size: 2em;
    text-transform: none;
  }
  p {
    max-width: 220px;
    margin: 0 auto;
    font-style: italic;
    font-size: 0.9em;
    line-height: 1.2em;
  }

  hr {
    display: inline-block;
    width: 1.6em;
    text-align: center;
    color: $white;
  }

  .gform_wrapper {
    width: 310px;
    margin: 1.8em auto 0 auto;

    input[type="submit"].gform_button {
      color: $black !important;
      width: 80%;
      text-align: center;
      border-radius: 0;
      &:hover {
        color: $white !important;
      }
    }
  }
  /* gform_wrapper */
}


/////// modal *********************************************


.sc-modal {
  $base-border-color: #dcdcdc;
  $base-border-radius: 3px;
  $base-background-color: #000;
  $base-font-size: 1em;
  $base-line-height: 1.5em;
  $action-color: #477dca;
  $dark-gray: #333;
  $light-gray: #ddd;
  $medium-screen: 40em;
  $large-screen: 53.75em;
  $base-font-color: $dark-gray;
  $modal-padding: 3em;
  $modal-background: $base-background-color;
  $modal-close-color: $light-gray;
  $modal-image-height: 135px;
  $modal-image-width: $modal-image-height;
  $modal-trigger-image-width: 300px;

  label {
    cursor: pointer;
    margin-bottom: 0;
  }

  label img {
    border-radius: $modal-trigger-image-width / 2;
    display: block;
    max-width: $modal-trigger-image-width;
  }

  .sc-modal-state {
    display: none;
  }

  .sc-modal-trigger {
    padding: 0.8em 1em;
  }

  .sc-modal-fade-screen { // overlay
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#000, 0.85);
    opacity: 0;
    padding-top: 0.6em;
    text-align: left;
    transition: opacity 0.25s ease;
    visibility: hidden;
    z-index: 99999999999;

    .sc-modal-bg {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      cursor: pointer;
    }
  }

  .sc-modal-close {

    position: absolute;
    top: ($modal-padding/2);
    right: ($modal-padding/2);
    width: 1.5em;
    height: 1.5em;
    background: $modal-background;
    cursor: pointer;

    &::after,
    &::before {
      position: absolute;
      top: 3px;
      right: 3px;
      bottom: 0;
      left: 50%;
      width: 0.15em;
      height: 1.5em;
      background: $modal-close-color;
      content: "";
      display: block;
      margin: -3px 0 0 -1px;
      transform: rotate(45deg);
    }

    &:hover::after,
    &:hover::before {
      background: darken($modal-close-color, 10%);
    }

    &::before {
      transform: rotate(-45deg);
    }
  }

  .sc-modal-inner {
    background: $modal-background;
    border-radius: $base-border-radius;
    margin: auto;
    margin-top: 0;
    max-height: 95%;
    overflow: auto;
    padding: ($modal-padding / 2);
    position: relative;
    transition: opacity 0.25s ease;
    width: 95%;

    h1 {
      color: $base-font-color;
      text-align: left;
      text-transform: capitalize;
    }

    p {
      color: $base-font-color;
      line-height: $base-line-height;
    }

    .sc-modal-intro {
      margin-bottom: 1em;
    }

    .sc-modal-content {
      color: $base-font-color;
    }

    a.cta {
      color: $base-background-color;
      display: inline-block;
      margin-right: 0.5em;
      margin-top: 1em;

      &:last-child {
        padding: 0 2em;
      }
    }
  }

  .sc-modal-state:checked + .sc-modal-fade-screen {
    opacity: 1;
    visibility: visible;
  }

  .sc-modal-state:checked + .sc-modal-fade-screen .sc-modal-inner {
    top: 0.5em;
  }
}

.sc-modal-open {
  overflow: hidden;

  header {
    display: none;
  }

}

// Based on code by Kasper Mikiewicz

.sc-video-wrapper {
  height: 0;
  overflow: hidden;
  padding-bottom: 56.25%; // For ratio 16:9. 75% if ratio is 4:3
  position: relative;

  embed,
  object,
  iframe {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }
}



///////******************************************************

@media screen and (max-width: 480px) {
  .copy-form-wrapper {
    .form-copy-wrapper {
      .camp-book {
        display: none;
      }

      .gform_wrapper input[type=submit].gform_button {
        text-align: center;
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  div.grid_section .section_inner.video-wrap {
    width: 100%;
  }

  div.grid_section .section_report .section_inner {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .header {

    .logo {
      width: auto;
      float: left;
      margin-top: 1.6em;
      margin-bottom: 1.8em;
    }

    .phone {
      width: auto;
      float: right;
      margin-top: 1.5em;
    }
  }
  /* .header */

  .video-outer-wrap {
    background-color: $secondary_color;
    .video-wrap {
      font-size: 0;

      div {
        width: 50%;
        display: inline-block;
      }
      img {
        display: block;
      }
    }

  }
  /* video-outer-wrap */

  .counter-wrapper {
    margin-top: -13px;

    .section_report {
      font-size: 0;

      .report {
        font-size: 16px;
        width: 33.333333%;
        display: inline-block;
        vertical-align: top;
        min-height: 260px;
        text-transform: uppercase;
      }
      .top-row {
        background: $primary_color;

      }

      .bottom-row {
        background: $secondary_color;

        .report {
          &:nth-of-type(odd) {
            background-color: $primary_color;
          }
        }
      }

    }
    /* section_report */
  }
  /* counter-wrapper */
  .sc-modal {

    .sc-modal-inner {
      max-height: 70%;
      padding: 3em;
      width: 60%;
    }

    .sc-modal-content {
      columns: 2 8em;
    }

  }
}

/* end media 768 */

@media screen and (min-width: 960px) {
  .copy-form-wrapper {
    .list-wrap {
      width: 50%;
      float: left;
    }
    /* list-wrap */

    .form-copy-wrapper {
      width: 49%;
      float: right;
    }
    /* form-copy-wrapper */

  }
  /* copy-form-wrapper */
}

@media(min-width: 1068px) {
  .sc-modal {

    .sc-modal-inner {
      width: 50%;
    }

    .sc-modal-fade-screen {
      padding-top: 10em;
    }

  }


}

@media screen and (min-width: 1240px) {
  .copy-form-wrapper {
    .list-wrap {
      width: 50%;
      float: left;
    }
    /* list-wrap */

    .form-copy-wrapper {
      width: 49%;
      float: right;
    }
    /* form-copy-wrapper */

  }
  /* copy-form-wrapper */
}